import React from 'react';
import { graphql } from 'gatsby';
import PostsLayout from 'components/Layout/Posts';
import SEO from 'components/SEO';

interface Props {
  data: {
    allMarkdownRemark: any;
    site: {
      siteMetadata: {
        title: string;
      };
    };
  };
  location: Location;
  pageContext: {
    lang: string;
  };
}

const PostsTemplate = ({ data, location, pageContext: { lang } }: Props) => {
  const {
    allMarkdownRemark: { edges: posts, pageInfo },
  } = data;

  return (
    <>
      <SEO lang={lang} />
      <PostsLayout
        posts={posts.map((p: { node: any }) => p.node)}
        pageInfo={{ ...pageInfo, defaultUrl: '/', lang }}
        location={location}
      />
    </>
  );
};

export default PostsTemplate;

export const pageQuery = graphql`
  query PostsQuery($skip: Int!, $limit: Int!, $lang: String!) {
    allMarkdownRemark(
      skip: $skip
      limit: $limit
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { fields: { lang: { eq: $lang } } }
    ) {
      edges {
        node {
          ...PostListQuery
        }
      }
      pageInfo {
        currentPage
        pageCount
      }
    }
  }
`;
